<template>
  <div class="advisor">
    <div class="header-btn-area">
      <el-button type="primary" icon="el-icon-plus" @click="showDialog = true"></el-button>
    </div>
    <el-table :data="advisorList">
      <el-table-column label="">
        <template slot-scope="scope">
          <img class="table-image" :src="scope.row.photoUrl" alt="">
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{ scope.row.name }}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{ scope.row.company }}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{ scope.row.intro }}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button size="mini" @click="showEdit(scope.$index)"></el-button>
          <el-button size="mini" type="danger" @click="showDel(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="" :visible.sync="showDialog" center>
      <el-form label-width="80px">
        <el-form-item label="">
          <el-input v-model="inputName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-upload class="upload-box" name="picture" action="/api/uploadFile" :on-success="onPhotoSuccess" :show-file-list="false">
            <i class="el-icon-plus"></i>
            <img :src="inputPhotoUrl" alt="">
          </el-upload>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="inputCompany"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="inputIntro"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="showDialog = false"></el-button>
        <el-button type="primary" @click="submit"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      showDialog: false,
      inputName: '',
      inputPhotoUrl: '',
      inputCompany: '',
      inputIntro: '',
      advisorId: '',
      advisorList: []
    }
  },
  mounted () {
    this.updateData()
  },
  methods: {
    updateData () {
      this.$http.post('/api/getProjAdvisorList', {
        projId: this.$route.params.id
      }).then((res) => {
        if (res.data.errcode === 0) {
          this.advisorList = res.data.data.dataList
        }
      })
    },
    onPhotoSuccess (res) {
      if (res.errcode === 0) {
        this.inputPhotoUrl = res.data.url
      }
    },
    showEdit (index) {
      var advisorInfo = this.advisorList[index]
      this.advisorId = advisorInfo.id
      this.inputName = advisorInfo.name
      this.inputPhotoUrl = advisorInfo.photoUrl
      this.inputCompany = advisorInfo.company
      this.inputIntro = advisorInfo.intro
      this.showDialog = true
    },
    showDel (advisorId) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.delAdvisor(advisorId)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    submit () {
      if (this.advisorId) {
        this.updAdvisor()
      } else {
        this.addAdvisor()
      }
    },
    addAdvisor () {
      this.$http.post('/api/addProjAdvisor', {
        projId: this.$route.params.id,
        name: this.inputName,
        photoUrl: this.inputPhotoUrl,
        company: this.inputCompany,
        intro: this.inputIntro
      }).then((res) => {
        if (res.data.errcode === 0) {
          this.$message({ type: 'success', message: '!' })
          this.inputName = ''
          this.inputPhotoUrl = ''
          this.inputCompany = ''
          this.inputIntro = ''
          this.showDialog = false
          this.updateData()
        }
      })
    },
    updAdvisor () {
      this.$http.post('/api/updProjAdvisor', {
        advisorId: this.advisorId,
        name: this.inputName,
        photoUrl: this.inputPhotoUrl,
        company: this.inputCompany,
        intro: this.inputIntro
      }).then((res) => {
        if (res.data.errcode === 0) {
          this.$message({ type: 'success', message: '!' })
          this.inputName = ''
          this.inputPhotoUrl = ''
          this.inputCompany = ''
          this.inputIntro = ''
          this.showDialog = false
          this.advisorId = ''
          this.updateData()
        }
      })
    },
    delAdvisor (advisorId) {
      this.$http.post('/api/delProjAdvisor', {
        advisorId: advisorId
      }).then((res) => {
        if (res.data.errcode === 0) {
          this.$message({ type: 'success', message: '!' })
          this.updateData()
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
</style>



